:root {
  --magenta-transparent: rgba(148, 49, 246, 0);
}
@-webkit-keyframes spin-360 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spin-360 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes blinks {
  0% {
    -webkit-transform: skew(15deg);
            transform: skew(15deg);
  }
  50% {
    -webkit-transform: skew(35deg);
            transform: skew(35deg);
  }
  100% {
    -webkit-transform: skew(15deg);
            transform: skew(15deg);
  }
}
@keyframes blinks {
  0% {
    -webkit-transform: skew(15deg);
            transform: skew(15deg);
  }
  50% {
    -webkit-transform: skew(35deg);
            transform: skew(35deg);
  }
  100% {
    -webkit-transform: skew(15deg);
            transform: skew(15deg);
  }
}
@-webkit-keyframes cascade-down {
  0% {
    -webkit-transform: translate(0, -50px);
            transform: translate(0, -50px);
  }
  100% {
    -webkit-transform: translate(0, 103vh);
            transform: translate(0, 103vh);
  }
}
@keyframes cascade-down {
  0% {
    -webkit-transform: translate(0, -50px);
            transform: translate(0, -50px);
  }
  100% {
    -webkit-transform: translate(0, 103vh);
            transform: translate(0, 103vh);
  }
}
.Particles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  overflow: hidden;
  z-index: 1;
  pointer-events: none;
}
.Particles:before {
  content: "";
  display: block;
  background: -webkit-linear-gradient(bottom, rgba(148, 49, 246, 0), var(--magenta));
  background: linear-gradient(0deg, rgba(148, 49, 246, 0), var(--magenta));
  background: -webkit-linear-gradient(bottom, var(--magenta-transparent), var(--magenta));
  background: linear-gradient(0deg, var(--magenta-transparent), var(--magenta));
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  position: absolute;
  z-index: 1;
}
.Particles-box {
  position: relative;
  top: -30px;
  -webkit-animation: 15s cascade-down linear;
          animation: 15s cascade-down linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 12;
          animation-iteration-count: 12;
  z-index: 0;
}
.Particles-box:nth-child(0) {
  left: 20%;
  -webkit-animation-delay: 0ms;
          animation-delay: 0ms;
}
.Particles-box:nth-child(0) .Particles-part {
  -webkit-animation-delay: 0ms;
          animation-delay: 0ms;
}
.Particles-box:nth-child(1) {
  left: 90%;
  -webkit-animation-delay: 1500ms;
          animation-delay: 1500ms;
}
.Particles-box:nth-child(1) .Particles-part {
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms;
}
.Particles-box:nth-child(2) {
  left: 80%;
  -webkit-animation-delay: 3000ms;
          animation-delay: 3000ms;
}
.Particles-box:nth-child(2) .Particles-part {
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms;
}
.Particles-box:nth-child(3) {
  left: 25%;
  -webkit-animation-delay: 4500ms;
          animation-delay: 4500ms;
}
.Particles-box:nth-child(3) .Particles-part {
  -webkit-animation-delay: 600ms;
          animation-delay: 600ms;
}
.Particles-box:nth-child(4) {
  left: 18%;
  -webkit-animation-delay: 6000ms;
          animation-delay: 6000ms;
}
.Particles-box:nth-child(4) .Particles-part {
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms;
}
.Particles-box:nth-child(5) {
  left: 85%;
  -webkit-animation-delay: 7500ms;
          animation-delay: 7500ms;
}
.Particles-box:nth-child(5) .Particles-part {
  -webkit-animation-delay: 1000ms;
          animation-delay: 1000ms;
}
.Particles-box:nth-child(6) {
  left: 75%;
  -webkit-animation-delay: 9000ms;
          animation-delay: 9000ms;
}
.Particles-box:nth-child(6) .Particles-part {
  -webkit-animation-delay: 1200ms;
          animation-delay: 1200ms;
}
.Particles-box:nth-child(7) {
  left: 30%;
  -webkit-animation-delay: 10500ms;
          animation-delay: 10500ms;
}
.Particles-box:nth-child(7) .Particles-part {
  -webkit-animation-delay: 1400ms;
          animation-delay: 1400ms;
}
.Particles-box:nth-child(8) {
  left: 20%;
  -webkit-animation-delay: 12000ms;
          animation-delay: 12000ms;
}
.Particles-box:nth-child(8) .Particles-part {
  -webkit-animation-delay: 1600ms;
