}
.Particles-box:nth-child(9) {
  left: 85%;
  -webkit-animation-delay: 13500ms;
          animation-delay: 13500ms;
}
.Particles-box:nth-child(9) .Particles-part {
  -webkit-animation-delay: 1800ms;
          animation-delay: 1800ms;
}
.Particles-part {
  position: absolute;
  width: 7px;
  height: 7px;
  background-color: transparent;
  -webkit-animation: spin-360 5000ms infinite linear;
          animation: spin-360 5000ms infinite linear;
}
.Particles-part:after {
  content: "";
  display: block;
  position: relative;
  top: 2px;
  left: 5px;
  width: 10px;
  height: 10px;
  -webkit-transform: skew(15deg);
          transform: skew(15deg);
  background: var(--gold);
  -webkit-animation: blinks 1000ms infinite linear;
          animation: blinks 1000ms infinite linear;
}

