.CardWrapper.is-cardBack .Card, .CardBack{
  display: none;
}

.CardWrapper.is-cardBack .CardBack{
  display: flex;
}

.CardWrapper.is-cardBack.is-loading .CardBack-inner{
  opacity: 0;
}


.CardWrapper{
  -webkit-transition: opacity 0.2s ease-in-out, -webkit-transform;
  transition: opacity 0.2s ease-in-out, -webkit-transform;
  transition: transform, opacity 0.2s ease-in-out;
  transition: transform, opacity 0.2s ease-in-out, -webkit-transform;
  opacity: 0;
}

.CardWrapper.is-layer1 {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}

.CardWrapper.is-layer2 {
  -webkit-transform: scale(0.9) translateY(-5px);
          transform: scale(0.9) translateY(-5px);
  opacity: 1;
}

.CardWrapper.is-layer3 {
  -webkit-transform: scale(0.8) translateY(-10px);
          transform: scale(0.8) translateY(-10px);
  opacity: 0.95;
}

.CardWrapper.is-layer4 {
  -webkit-transform: scale(0.7) translateY(-15px);
          transform: scale(0.7) translateY(-15px);
}

